*{-webkit-box-sizing:border-box;box-sizing:border-box}
.logo img{width:100%}
.logo{width:73px;float:left}
.header{float:left;width:310px;list-style:none;margin-top:30px;padding-left:19px}
ul.header li{float:left;margin:0 18px}
ul.header li a{color:#fff;text-decoration:none}
body{font-family:'Nunito',sans-serif;margin:0}
ul.header li a.active{color:#fff;font-weight:700}
.content{width:100%;float:left;overflow:hidden}
.header-cont{float:left;width:100%;position:fixed;background:transparent;padding-top:21px}
.inner-cont{max-width:1154px;margin:auto;width:90%}
.btn{width:auto;padding:15px 38px;font-size:16px;border-radius:50px;text-decoration:none}
.header-opo{float:right;padding-top:22px}
.account{float:left;text-align:center;color:#fff;font-size:13px;width:100px;padding-top:7px;line-height:21px}
.header-cont .btn{padding:10px 23px;color:#61cbb2;font-size:13px;float:left;margin-left:50px;background:#fff}
.lang-switcher{float:left;margin-left:21px;position:relative}
.flag{width:35px;margin-top:1px}
.flag img{width:100%}
footer{float:left;width:100%;padding-top:27px}
footer .logo{width:63px;position:relative}
ul.footer-links{float:right;list-style:none;padding:0;margin-top:13px}
ul.footer-links a{color:#252c39;text-decoration:none;letter-spacing:1px;font-size:13px}
ul.footer-links li{float:left;width:auto;padding:0 21px}
.logo-slogan{width:100px;display:inline-block;font-size:8px;position:absolute;top:100%;right:-70px;color:#e1e1e1}
.is-sticky.active .header-cont{background:#fff;-webkit-box-shadow:7px 3px 13px #888;box-shadow:7px 3px 13px #888}
.is-sticky.active .header-cont ul a{color:#909090}
header .logo img:last-of-type{display:none}
.active .logo img:first-of-type{display:none}
.active .logo img:last-of-type{display:block!important}
.is-sticky.active ul.header li a.active{color:#1e2534}
.active .account{color:#1e2534}
.active .header-cont .btn{background:#61cbb2;color:#fff;border-color:#61cbb2}
.lang-switcher .popup{opacity:0;background:#fff;width:140px;height:50px;border-radius:5px;text-align:center;padding-top:14px;position:absolute;top:60px;right:-53px;-webkit-transition:all .5s ease;-o-transition:all .5s ease;transition:all .5s ease}
.lang-switcher .popup:before{bottom:100%;left:50%;border:solid transparent;content:" ";height:0;width:0;position:absolute;pointer-events:none;border-color:rgba(194,225,245,0);border-bottom-color:#fff;border-width:12px;margin-left:-10px}
.lang-switcher:hover .popup{opacity:1}
.mobile{display:none}
@media screen and (max-width: 1024px) {
.mobile{display:block}
header .header-cont,header .header-cont,.is-sticky.active .header-cont{width:100%;float:left;height:600px;background:url(/static/media/mobileNavBg.e4c45b20.png);background-size:cover;background-position:0}
.is-sticky.active ul.header li a.active{color:#fff}
ul.header{padding-left:0;margin-bottom:0}
ul.header li{width:100%;height:40px}
.account{display:none}
.header-cont .btn{display:none}
.lang-switcher{width:100%}
.header-opo{width:100%;padding-top:0}
.flag{display:none}
.lang-switcher .popup{display:block;position:unset;color:#fff;opacity:1;background:transparent;text-align:left;padding-top:0}
.lang-switcher .popup:before{display:none}
}



